




















































































































#app {
	&::after {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: -999;
		display: block;
		content: "";
		background: linear-gradient(
			-3deg,
			var(--md-theme-default-accent) 15%,
			#fff 15%
		);
	}
}
